.statistics {
    margin: 30px auto 0;
    width: 300px;
  }
  .title {
    padding: 24px;
    background-color: #ffffff;
    font-size: 19px;
    color: #76848f;
    text-transform: uppercase;
    text-align: center;
  }
  .statList {
    display: flex;
    justify-content: space-around;
  }
  .item {
    display: flex;
    flex-direction: column;
    width: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
    gap: 5px;
    text-align: center;
    color: #ffffff;
  }
  .item:first-child {
    border-bottom-left-radius: 5px;
  }
  .item:last-child {
    border-bottom-right-radius: 5px;
  }
  .label {
    font-size: 13px;
  }
  .percentage {
    font-size: 18px;
  }